  font-size: calc(16px + 0.5vh);
  font-family: system-ui;
  --main-font-color: white;
  --main-background-color: black;
  font-family: Times, serif, system-ui;
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

a {
  color: rgb(98, 137, 165);
}

a:visited {
  color: rgb(98, 137, 165);
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--main-background-color);
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  padding: 5px;
}

header {
  background: linear-gradient(90deg, rgb(0, 0, 0, 0.5), rgb(0, 0, 0, 0.8), rgb(0, 0, 0, 0.5));
  color: var(--main-font-color);
  border-radius: 5px;
  width: 100%;
  margin: 5px;
  text-align: center;
}

main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(90deg, rgb(0, 0, 0, 0.5), rgb(0, 0, 0, 0.8), rgb(0, 0, 0, 0.5));
  color: var(--main-font-color);
  padding: 5px;
  border-radius: 5px;
  width: 100%;
}

footer {
  text-align: center;
  width: 100%;
  background: linear-gradient(90deg, rgb(0, 0, 0, 0.5), rgb(0, 0, 0, 0.8), rgb(0, 0, 0, 0.5));
  color: var(--main-font-color);
  padding: 5px;
  border-radius: 5px;
}

.credit {
  font-size: 0.7rem;
}

.grid-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}

.player-grid {
  display: grid;
  grid-template-columns: repeat(10, 33px);
  grid-template-rows: repeat(10, 33px);
  gap: 1px;
  padding: 2px;
  border: 2px outset rgb(228, 228, 228, 0.5);
  border-radius: 5px;
  opacity: 0;
  z-index: -1;
  position: absolute;
  background-color: var(--main-background-color);
  transition: 250ms linear;
  overflow: hidden;
}

.computer-grid {
  display: grid;
  grid-template-columns: repeat(10, 33px);
  grid-template-rows: repeat(10, 33px);
  gap: 1px;
  padding: 2px;
  border: 2px outset rgb(228, 228, 228, 0.5);
  border-radius: 5px;
  opacity: 0;
  z-index: -1;
  position: relative;
  background-color: var(--main-background-color);
  transition: 250ms linear;
  overflow: hidden;
}

.gridSquare {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  border-radius: 1px;
  border: 1px solid rgb(232, 232, 232, 0.2);
  transition: 500ms linear;
  font-size: 0.5rem;
  cursor: crosshair;
  text-wrap: pretty;
  hyphens: auto;
  font-weight: 800;
}

footer {
  font-size: 0.8rem;
  margin: 5px;
  padding: 5px;
}

.commentary {
  height: 2rem;
  font-size: 1rem;
}
.screenMode {
  display: flex;
  height: 2rem;
  width: 100%;
  justify-content: center;
  align-items: center;
  font-size: 0.7rem;
}

button {
  width: 130px;
  display: inline-block;
  padding: 3px 30px 3px 30px;
  font-size: 0.8rem;
  border-radius: 15px;
  background: var(--main-background-color);
  color: var(--main-font-color);
  border: 2px outset black;
  outline: 1px solid rgb(255, 255, 255, 0.25);
  margin: 5px;
}

.movesMade {
  font-size: 0.7rem;
  margin-top: 15px;
}

@keyframes horizontal-shaking {
  0% { transform: translateX(0) }
  25% { transform: translateX(5px) }
  50% { transform: translateX(-5px) }
  75% { transform: translateX(5px) }
  100% { transform: translateX(0) }
 }

 @media screen and (min-width: 600px) {

  body {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--main-background-color);
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
