    border: 5px outset rgb(228, 228, 228, 0.5);
    border-radius: 5px;
    opacity: 0;
    z-index: -1;
    position: absolute;
    background-color: var(--main-background-color);
    transition: 250ms linear;
    overflow: hidden;
  }
  
  .computer-grid {
    display: grid;
    grid-template-columns: repeat(10, 50px);
    grid-template-rows: repeat(10, 50px);
    gap: 1px;
    padding: 2px;
    border: 5px outset rgb(228, 228, 228, 0.5);
    border-radius: 5px;
    opacity: 0;
    z-index: -1;
    position: relative;
    background-color: var(--main-background-color);
    transition: 250ms linear;
    overflow: hidden;
  }
}

 @media screen and (min-width: 860px) {

  body {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--main-background-color);
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
